<template>
  <div class="ad-container">
    <Title class="page-title" name="광고문의&제휴" :more="false"></Title>
    
    <van-space direction="vertical" fill>
    <!-- <div class="" style="display: flex; justify-content: center; align-items: center; ">  
        <van-button  color="black"   @click="goFriend()" >
          <p style="font-size: 30px; color: yellow;">광고문의</p>
        </van-button>
      </div> -->
    
      <div class="ad-wrapper">
        <div class="t2">텔레그램 : <span>@mong808</span></div>
        <div class="t3">
          * 사칭에 주의하시기 바랍니다.<br>
          * 연락주시면 최대한 빨리 답변드리겠습니다.
        </div>
      </div>
    
      

      <Title class="page-title" style="margin-top: 2%;" name="제휴업체" :more="false"></Title>
      
      <div class="ad-wrapper">
        <div class="content" >
        <van-config-provider  :theme-vars="themeVars" theme="dark">
          <van-grid :gutter="20" :clickable="true" :column-num="5">
            <van-grid-item   
            v-for="(d,i) in friendLinks"
            :key="i"
            :text="d.text" 
            @click="goFriend(d.link)"
            class="hover-hand" />

          </van-grid>
        </van-config-provider>
  </div>
      </div>
  </van-space>
  </div>
</template>
<script>
  import * as api from '@/api'
  import Title from "@/components/Title.vue";
  import Card from "@/components/Card.vue";
  import {mapActions,mapState} from 'vuex';
  // import friendLinks from '@/assets/friendLinks.json'
  export default {
    name: "Index",
    components: {Title, Card},
    data() {
      return {
        themeVars:{
          gridItemContentBackground: "#191d25",
          gridItemContentActiveColor: "red",
          gridItemTextFontSize: "20px"
        },
        friendLinks:[]
      }
    },
    watch: {

    },
    computed: {
      ...mapState({
        language: state => state.common.language,
      }),
    },
    methods: {
      ...mapActions({
        toggleLoading: 'common/toggleLoading',
      }),
      goFriend(url) {
        console.log(url);
        window.open(url,"_blank");
      },
      async getFriendLink(){
        let {code,rows} = await api.getFriendLink({
            pageNum: 1,
            pageSize: 100,
        })
        // console.log('rows=>',rows)
        if(code==200){
          // console.log(rows)
          this.friendLinks=rows
        }
      }
    },
    mounted() {
      this.getFriendLink()
    },
  }
</script>

